// VARIABLES AND MIXINS

$color-white: #ffffff;
$color-light-grey: #F0F3FD;
$color-light-grey-2: #E0E3EB;
$color-dark-grey: #888B97;
$color-black: #000000;

$main-color: #1B1C57;
$main-color-dark: #0E1735;
$added-color: #626687;
$added-color-dark: #3C4563;
$pretitle-color: #F59E0B;

$btn-color-light: #D1FAE5;
$btn-color-dark: #10B981;
$btn-font-green: #047857;

$btn-popular-font: #EF4444;
$btn-popular-color: #FEE2E2;

$icon-mail: #3B82F6;
$btn-new-font: #1D4ED8;
$btn-new-color: #DBEAFE;

$text-shadow: -1px 0 $main-color, 0 1px $main-color, 1px 0 $main-color, 0 -1px $main-color;

$mb-5: 5rem;

@mixin display-between {
    display: flex;
    justify-content: space-between; }

@mixin display-align {
    display: flex;
    align-items: center; }

@mixin display-between-center {
    display: flex;
    justify-content: space-between;
    align-items: center; }

@mixin font14-line18 {
    font-size: 1.4rem;
    line-height: 1.8rem; }

@mixin font14-line22 {
    font-size: 1.4rem;
    line-height: 2.2rem; }

@mixin font14-line24 {
    font-size: 1.4rem;
    line-height: 2.4rem; }

@mixin font16-line24 {
    font-size: 1.6rem;
    line-height: 2.4rem; }

@mixin font16-line28 {
    font-size: 1.6rem;
    line-height: 2.8rem; }

@mixin content-position {
    content: "";
    position: absolute; }

// COMMON STYLES

*, ::after, ::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box; }

::-webkit-scrollbar {
    width: 0; }

html {
    font-size: 62.5%;
    scroll-behavior: smooth; }

body {
    color: $main-color;
    font-size: 1.6rem;
    font-family: "Lexend", sans-serif;
    background-color: $color-white;
    overflow-x: hidden;
    padding-bottom: 12rem; }

img {
    height: auto;
    display: block; }

h1, h2, h3, h4 {
    margin: 0; }

a {
    @include font14-line18;
    color: $color-light-grey;
    font-weight: 600;
    text-decoration: none;
    transition: .3s; }

a,
button {
    cursor: pointer; }

p {
    @include font16-line28;
    text-transform: none; }

ul, ol {
    list-style: none; }

.container {
    width: 1200px;
    margin: 0 auto; }

.background-wrapper {
    position: relative;
    margin: 0 auto;
    max-width: 144rem;

    &::before {
        @include content-position;
        top: -230px;
        left: -140px;
        background-image: url(../img/background-1.png);
        background-repeat: no-repeat;
        background-position: top left;
        background-size: 800px;
        width: 800px;
        height: 800px;
        z-index: -1; } }

.radius {
    border-radius: 32px; }

.btn-transparent {
    display: inline-block;
    padding: 7.5px 16.5px;
    background-color: rgba($color-white, 0.1);
    border: 1px solid rgba($color-white, 0.3); }

.btn-light_green {
    @include font14-line22;
    font-weight: 600;
    border: none;
    color: $btn-font-green;
    background-color: $btn-color-light;
    padding: 12px 24.5px; }

.btn-dark_green {
    @include display-align;
    @include font14-line22;
    font-weight: 600;
    border: none;
    padding: 13px 21.5px;
    color: $color-white;
    background-color: $btn-color-dark;
    text-transform: capitalize; }

.list {
    display: flex;

    &::after {
        content: "";
        background: url(../icon/arrow.svg) center/cover no-repeat;
        width: 18px;
        height: 18px;
        margin-left: 1rem; } }

.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden; }

.title {
    font-weight: 600;
    font-size: 3.2rem;
    line-height: 4rem;
    text-transform: capitalize; }

// HEADER

.main-header {
    padding-top: 4rem; }

.main-nav {
    @include display-between-center;
    background-color: transparent;
    width: 1200px;
    margin-bottom: 9.6rem;

    &__logo {
        margin-right: auto; } }

.navigation {
    @include display-between-center;
    width: 29%;
    margin-right: 4%; }

// SECTION HERO

.search {
    @include font14-line22;
    color: $color-dark-grey;
    font-weight: 500;

    input:focus {
        outline: none; }

    label {
        position: absolute;
        clip: rect(0 0 0 0); } }

.icon-location {
    color: $pretitle-color; }

.hero {
    margin-bottom: 21rem;

    &__right {
        position: relative;
        border-radius: 0px 0px 0px 80px;

        &_img {
            position: absolute;
            bottom: -91px;
            right: -117px;
            z-index: -1; } }

    &__title {
        width: 30%;
        font-size: 4rem;
        font-weight: 600;
        line-height: 5rem;
        text-transform: capitalize;
        text-shadow: $text-shadow;
        margin-bottom: 2.4rem;

        span {
            color: $color-white; } }

    &__info {
        width: 39%;
        color: $added-color;
        opacity: 0.75;
        margin-bottom: 3.2rem; }

    &__search {
        position: relative;
        margin-bottom: 3.2rem;

        i {
            display: block;
            position: absolute;
            top: 16px;
            left: 25px;
            font-size: 2.2rem; }

        input {
            width: 41.35%;
            padding: 16.2px 65px;
            border: 1px solid $color-light-grey-2; } }

    &__button {
        position: absolute;
        top: 4px;
        left: 382px;

        &::after {
            content: "";
            background: url(../icon/arrow_slider.svg) center/cover no-repeat;
            width: 7px;
            height: 14px;
            transform: rotate(180deg);
            margin-left: 1.1rem; } }

    &__partnership {
        @include display-between;
        width: 36%; }

    &__partnership-info {
        color: $color-dark-grey;
        font-size: 1.6rem;
        line-height: 3.2rem;
        text-transform: capitalize; }

    &__boxes {
        @include display-between;
        position: absolute;
        width: 54rem;
        height: 11.4rem;
        right: 3px;
        top: -60px;

        &_big {
            @include display-align;
            padding: 2.9rem 2.6rem;
            background-color: $color-white; }

        &_small {
            @include display-align;
            padding: 2.9rem 2.4rem;
            background-color: $color-white;

            img {
                margin-right: 1.6rem; } }

        p {
            @include font16-line24;
            color: $main-color;
            font-weight: 600;
            text-transform: capitalize;
            margin-bottom: 2px; }

        span {
            color: $added-color;
            font-size: 12px;
            line-height: 2rem;
            text-transform: capitalize; } } }

.people {
    display: flex;
    width: 11.2rem;
    margin-right: 1.6rem; }

.people li img {
    border-radius: 50%;
    outline: 3px solid $color-white;
    outline-offset: -3px; }

.people li:nth-child(2) {
    transform: translateX(-27px); }

.people li:nth-child(3) {
    transform: translateX(-53px); }

// SECTION FEATURES

.section {

    &__container {
        margin-left: 4rem;
        position: relative; }

    &__pretitle {
        @include font14-line18;
        color: $pretitle-color;
        font-weight: 500;
        text-transform: capitalize;
        position: relative;
        margin: 0 0 1.2rem 4rem;

        &::before {
            @include content-position;
            width: 32px;
            height: 1px;
            top: 8px;
            left: -40px;
            background-color: $pretitle-color; } } }

.features {
    margin-bottom: 12rem;

    &__subtitle,
    &__info {
        font-weight: 500;
        line-height: 3.2rem; }

    &__subtitle {
        font-size: 2.4rem;
        margin-bottom: .8rem; }

    &__price {
        font-size: 2rem;
        color: $added-color-dark;
        margin-bottom: 2.4rem; }

    &__block {
        @include display-between-center;
        width: 80%;
        margin-bottom: 4rem; }

    &__navigation {
        @include display-between;
        width: 55%; }

    &__item {
        user-select: none; }

    .swiper-container {
        position: relative;
        width: 100%;
        height: 555px;
        margin: 0 auto; }

    .swiper {
        width: 100%;
        height: 100%;
        overflow: visible !important;

        &-slide {
            width: 350px;
            min-width: 350px;
            height: 552px;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            background: transparent;
            user-select: none; }

        &-slide .swiper-img {
            border-radius: 24px;
            margin-bottom: 2.4rem; } }

    .swiper-button-prev,
    .swiper-button-next {
        @include display-align;
        justify-content: center;
        position: absolute;
        top: -70px;
        left: auto;
        width: 6rem;
        height: 5.2rem;
        border: none;
        box-sizing: border-box;
        background-color: $btn-color-dark;
        cursor: pointer;
        z-index: 1;

        svg {
            fill: $color-white; }

        &:hover {
            opacity: 0.7; }
        &:active {
            opacity: 0.5; } }

    .swiper-button-next {
        right: 2px;
        transform: rotate(180deg); }

    .swiper-button-prev {
        right: 85px; }

    .swiper-button-prev::after,
    .swiper-button-next::after {
        display: none !important; }

    .swiper-button-disabled {
        opacity: 1 !important;
        background-color: $color-light-grey-2 !important;

        svg {
            fill: $added-color-dark; } } }

.block-button {
    @include display-between-center;
    color: $color-dark-grey;
    font-weight: 500;
    font-size: 1.8rem;
    line-height: 2.8rem;
    border: 1px solid $color-light-grey-2;
    padding: 11px 25px;
    transition: all .3s;

    i {
        margin-right: 11px;
        color: $color-dark-grey; }

    &:hover {
        border: 1px solid transparent;
        background-color: $btn-color-light;
        color: $btn-color-dark; }

    &:hover i {
        color: $btn-color-dark; } }

.img__button {
    @include display-align;
    @include font14-line18;
    font-weight: 500;
    text-transform: capitalize;
    position: absolute;
    top: 335px;
    margin-left: 16px;
    padding: 8px 15px;

    i {
        margin-right: 12px; }

    &_popular {
        width: 11.2rem;
        color: $btn-popular-font;
        background-color: $btn-popular-color; }

    &_new {
        width: 13.5rem;
        color: $btn-new-font;
        background-color: $btn-new-color; }

    &_best {
        width: 13rem;
        color: $btn-font-green;
        background-color: $btn-color-light; } }

.slide__info {
    color: $main-color-dark;
    text-transform: capitalize;
    text-align: left;

    &_people {
        @include display-align; }

    &_people img {
        margin: 0 1.6rem 0 0; }

    &_contacts {
        @include display-between;
        align-items: flex-start;
        flex-direction: column;
        font-weight: 500; }

    &_contacts p {
        font-size: 1.8rem;
        line-height: 2.4rem;
        margin-bottom: .4rem; }

    &_contacts span {
        @include font14-line22;
        color: $color-dark-grey;
        text-transform: capitalize; } }

// SECTION TOUR

.tour .section__container {
    max-width: 600px; }

.tour {
    position: relative;
    margin-bottom: 15.3rem;

    &::before {
        @include content-position;
        top: -300px;
        right: -330px;
        background-image: url(../img/background-2.png);
        background-repeat: no-repeat;
        background-size: 700px;
        width: 700px;
        height: 700px;
        z-index: -1; }

    &__title {
        margin-bottom: 1.6rem; }

    &__info,
    &__house {
        @include font16-line28;
        margin-bottom: 1.6rem; }

    &__info {
        width: 69%;
        opacity: 0.75;
        color: $added-color; }

    &__house {
        color: $main-color;
        font-weight: 600;
        text-transform: capitalize; }

    &__button {
        padding: 12px 16.5px;

        i {
            font-size: 2.4rem;
            margin-right: 1.6rem; } } }

.house__details {
    @include display-between;
    width: 85%;
    height: 7.7rem;
    flex-wrap: wrap;
    flex-direction: column;
    position: relative;
    margin-bottom: 6.5rem;

    &_item {
        @include display-align; }

    i {
        font-size: 3.2rem;
        color: $color-dark-grey;
        margin-right: 1.8rem; }

    p {
        color: $added-color-dark;
        font-weight: 500;
        font-size: 1.6rem;
        line-height: 2rem;
        text-transform: capitalize; }

    &::before {
        @include content-position;
        bottom: -35px;
        width: 440px;
        height: 2px;
        background-color: $color-light-grey; } }

.tour__left .slide__info_people img {
    margin-right: 2.4rem; }

.tour__left .slide__info_people span {
    @include font16-line24;
    font-weight: 500; }

.house__contacts {
    display: flex;
    align-items: center;

    .slide__info_people {
        margin-right: 5.7rem; } }

.tour__right {
    position: absolute;
    top: -35px;
    right: 0;

    &-list {
        display: flex;
        align-items: flex-end;
        z-index: 1;
        position: absolute;
        bottom: -43px;
        left: -40px; }

    &-item img {
        border-radius: 4px; } }

.tour__right-item:nth-child(1) {
    margin-right: 1.2rem; }

.tour__right-item:nth-child(2) {
    margin-right: .8rem; }

// SECTION REVIEWS

.pretitle-center {
    text-align: center;
    margin: 0 auto 1.2rem;

    &::before {
        top: -50%;
        left: 50%;
        transform: translate(-50%, -50%); } }

.reviews {
    margin-bottom: 27.3rem;

    &__title {
        text-align: center;
        margin-bottom: 4rem; }

    &__info {
        width: 82.75%;
        height: 62.3%;
        background-color: $color-white;
        box-shadow: 0px 9px 32px rgba(89, 92, 219, 0.05);
        border-radius: 16px;
        position: absolute;
        z-index: 1;
        top: 255px;
        padding: 3.3rem;
        text-align: left;

        .slide__info_people img {
            width: 40px !important;
            height: 40px !important;
            margin-right: 2.4rem; }

        .slide__info_contacts p {
            @include font14-line22;
            color: $main-color-dark; } }

    &__subtitle {
        font-weight: 600;
        font-size: 2rem;
        line-height: 2.5rem;
        text-transform: capitalize;
        margin-bottom: 1.6rem; }

    &__text {
        @include font14-line24;
        opacity: 0.75;
        color: $added-color;
        margin-bottom: 2.4rem; }

    &__box {
        @include display-between;
        align-items: flex-end;
        width: 85%; }

    &__rate {
        @include display-between-center;

        i {
            color: $pretitle-color;
            margin-right: 1.6rem; }

        p {
            color: $added-color-dark;
            font-weight: 600;
            font-size: 2rem;
            line-height: 2.8rem; } }

    .swiper-container {
        position: relative;
        width: 100%;
        margin: 0 auto; }

    .swiper {
        width: 100%;
        height: 100%;
        overflow: visible !important;

        &-slide {
            @include display-align;
            justify-content: center;
            width: 74rem;
            min-width: 74rem;
            height: 40rem;
            text-align: center;
            font-size: 1.8rem;
            background: $color-white;
            user-select: none;
            position: relative;

            img {
                display: block;
                width: auto !important;
                height: auto !important;
                object-fit: cover;
                border-radius: 8px; } }

        &-pagination {
            bottom: -150px !important; }

        &-pagination-bullet {
            background: $color-light-grey-2 !important;
            opacity: 1 !important;
            margin: 0 8px !important; }

        &-pagination-bullet-active {
            background: $added-color-dark !important; } } }

// SECTION FIND

.find {
    margin-bottom: 12rem;
    position: relative;

    &__subcontainer {
        @include display-between;
        align-items: flex-start; }

    &::before {
        @include content-position;
        top: -120px;
        left: -360px;
        background-image: url(../img/background-3.png);
        background-repeat: no-repeat;
        background-size: 700px;
        width: 700px;
        height: 850px;
        z-index: -1; }

    &__title {
        text-align: center;
        margin: 0 auto 2.4rem;
        width: 36%; }

    &__button {
        padding: 12px 16.5px;
        margin: 0 auto 4rem; }

    &__left,
    &__right {
        width: 50%; }

    &__left {
        @include display-between;
        flex-direction: column;
        margin-right: 6.8rem; }

    &__img {
        border-radius: 16px;

        &:not(:last-child) {
            margin-right: 3.2rem; }

        &_right {
            margin-bottom: 2.4rem; } }

    &__subcontainer {
        @include display-between-center;

        &:not(:last-child) {
            margin-bottom: 4rem; }

        &_title {
            font-weight: 500;
            font-size: 1.8rem;
            line-height: 3.3rem;
            text-transform: capitalize;
            margin-bottom: 1.6rem; }

        &_text {
            @include font14-line24;
            color: $added-color;
            opacity: 0.75;
            margin-bottom: 1.6rem; }

        &_time {
            @include display-align;

            p {
                @include font14-line24;
                color: $color-dark-grey; } } } }

.find__subcontainer_info .slide__info_people {
    margin-bottom: 1.2rem; }

.find__subcontainer_info .slide__info_people p {
    @include font14-line22;
    color: $added-color-dark;
    font-weight: 400; }

.find__right .find__subcontainer_title {
    font-size: 2.4rem;
    margin-bottom: 1.2rem; }

.icon-clock {
    color: $color-dark-grey;
    margin-right: 1.4rem; }

// SECTION SUBSCRIBE

.icon-email {
    color: $icon-mail; }

.subscribe__imgages .subscrbe__img:nth-child(1) {
    margin-bottom: 5.4rem; }

.subscribe__imgages:nth-child(2) .subscrbe__img:nth-child(1) {
    margin-bottom: 7.1rem; }

.subscribe__right .subscribe__imgages:nth-child(1) .subscrbe__img:nth-child(1) {
    margin-bottom: 6.1rem; }

.subscribe__right .subscribe__imgages:nth-child(2) {
    margin-top: 6rem; }

.subscribe__right .subscribe__imgages:nth-child(2) .subscrbe__img:nth-child(1) {
    margin-bottom: 5.6rem; }

.subscribe__imgages:nth-child(1) {
    margin-right: 7.9rem; }

.subscribe__imgages:nth-child(2) {
    margin-top: 3rem; }

.subscribe {
    @include display-between;
    align-items: flex-start;
    height: 31.2rem;
    padding: 3.8rem 7rem 6.5rem 4.4rem;
    background: url(../img/subscribe.jpg) center/cover no-repeat;
    margin-bottom: 120px;

    &__left,
    &__right {
        display: flex; }

    &__left {
        margin-right: 10.3rem; }

    &__center {
        margin-top: auto;
        margin-right: 8.2rem; }

    &__imgages {
        @include display-align;
        flex-direction: column;

        img {
            border-radius: 24px;
            outline: 2px solid $color-white;
            outline-offset: 0px; } }

    &__title {
        line-height: 4.8rem;
        text-align: center;
        margin-bottom: 3.2rem; }

    &__search {
        position: relative;

        i {
            display: block;
            position: absolute;
            top: 16px;
            left: 25px;
            width: 18px;
            font-size: 2.2rem;
            z-index: 1; }

        input {
            position: relative;
            width: 49.6rem;
            padding: 1.6rem 6.5rem;
            border: none; } }

    &__button {
        position: absolute;
        top: 4px;
        left: 363px;
        padding: 12px 16.1px; } }

// FOOTER

.social {
    @include display-between-center;
    width: 47%;

    &__icon {
        font-size: 2rem;
        color: $added-color-dark; }

    &__link {
        padding: 1rem; } }

.property,
.article,
.contact {
    text-transform: capitalize;

    &__title {
        color: $main-color-dark;
        font-weight: 600;
        font-size: 1.8rem;
        line-height: 2.2rem; }

    &__link {
        @include font14-line18;
        color: $color-dark-grey;
        font-weight: 400; } }

.contact .contact__item:nth-child(4) .contact__link {
    text-transform: none; }

.main-footer {
    display: flex;
    position: relative;

    &::before {
        @include content-position;
        right: -290px;
        bottom: -140px;
        background-image: url(../img/background-4.png);
        background-repeat: no-repeat;
        background-size: 950px;
        width: 950px;
        height: 503px;
        z-index: -1; }

    &__left,
    &__right {
        display: flex; }

    &__left {
        width: 26.5%;
        flex-direction: column;
        margin-right: auto; }

    &__right {
        width: 51%;
        justify-content: space-between;

        &_title {
            margin-bottom: 2.4rem; }

        &_link {
            margin-bottom: 1.8rem; } }

    &__logo {
        margin-bottom: 1.6rem; }

    &__info {
        @include font14-line24;
        color: $added-color;
        opacity: 0.75;
        margin-bottom: 2.4rem; } }

.hamburger,
.mobile-menu {
    display: none; }

// MEDIA QUERIES

@media (max-width: 1400px) {

    .background-wrapper {
        width: 90vw; }

    .container {
        width: 1170px; }

    .hero {
        &__right_img {
            bottom: -91px;
            right: -145px; }

        &__title {
            width: 40%; }

        &__button {
            left: 370px; }

        &__boxes {
            right: -25px; } }

    .find__title {
        width: 37%; } }

@media (max-width: 1199px) {

    .container {
        width: 960px; }

    html {
        font-size: 62%; }

    .hero__right_img {
        display: none; }

    .main-nav {
        width: 100%; }

    .navigation {
        width: 55%;
